export class Result {
  action: string;
  x: number;
  y: number;
  answer: number;
  cached: boolean

  constructor (obj: {
    action: string,
    x: number,
    y: number,
    answer: number,
    cached: boolean,
  }) {
    this.action = obj.action
    this.x = obj.x
    this.y = obj.y
    this.answer = obj.answer
    this.cached = obj.cached
  }
}
