import { Result } from '@/models/result'

export class HttpService {
  public async getResult (action: string, x: number, y: number): Promise<Result> {
    const res: Response = await fetch(`https://challenge-hfpike7vfq-uc.a.run.app/${action}?x=${x}&y=${y}`, {
      method: 'get',
      headers: {
        'content-type': 'application/json'
      }
    })
    const json = await res.json()

    return new Result(json)
  }
}
